.Calendar {
    Width: 350px;
    Height: 240px;
    Font-size: 16px;
    display: grid;
    grid-template-columns: 44px 1fr 44px;
    grid-template-rows: 44px 1fr;
    row-gap: 16px;

    .Header {
        grid-area: 1/2/2/3;
        height: 44px;
        font-size: 15px;
        line-height: 44px;
    }

    .Select {
        Width: 44px;
        Height: 44px;
        line-height: 44px;
        Background-color: white;

        &.Pre {
            grid-area: 1/1/2/2;
        }

        &.Next {
            grid-area: 1/3/2/4;
        }
    }

    .Select:hover {
        width: 44px;
        height: 44px;
        line-height: 44px;
        background-color: #e6e6e6;
    }

    .Body {
        grid-area: 2/1/3/4;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;

        .Day {
            Width: 50px;
            Height: 36px;
            font-size: 13px;
            line-height: 36px;
            background-color: white;

            &:hover {
                background-color: #e6e6e6;
            }

            &.currentDay {
                background-color: #ffff76;
            }

            &.not-CurrentMonth {
                color: #757575;
            }

            &.active {
                background-color: #006edc;
                color: white;

                &:hover {
                    background-color: #006edc;
                }
            }
        }
    }
}