.Calendar {
  Width: 350px;
  Height: 240px;
  Font-size: 16px;
  display: grid;
  grid-template-columns: 44px 1fr 44px;
  grid-template-rows: 44px 1fr;
  row-gap: 16px;
}
.Calendar .Header {
  grid-area: 1/2/2/3;
  height: 44px;
  font-size: 15px;
  line-height: 44px;
}
.Calendar .Select {
  Width: 44px;
  Height: 44px;
  line-height: 44px;
  Background-color: white;
}
.Calendar .Select.Pre {
  grid-area: 1/1/2/2;
}
.Calendar .Select.Next {
  grid-area: 1/3/2/4;
}
.Calendar .Select:hover {
  width: 44px;
  height: 44px;
  line-height: 44px;
  background-color: #e6e6e6;
}
.Calendar .Body {
  grid-area: 2/1/3/4;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.Calendar .Body .Day {
  Width: 50px;
  Height: 36px;
  font-size: 13px;
  line-height: 36px;
  background-color: white;
}
.Calendar .Body .Day:hover {
  background-color: #e6e6e6;
}
.Calendar .Body .Day.currentDay {
  background-color: #ffff76;
}
.Calendar .Body .Day.not-CurrentMonth {
  color: #757575;
}
.Calendar .Body .Day.active {
  background-color: #006edc;
  color: white;
}
.Calendar .Body .Day.active:hover {
  background-color: #006edc;
}/*# sourceMappingURL=Calendar.css.map */